* {
    box-sizing: border-box;
}

body {
  background-color: black;
  font-family: 'Open Sans', "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.grid {
  .cell, .cell-settings {
    position: relative;
    display: inline-block;
    border: 4px solid black;
    width: calc(100% / 8);

    background-color: gray;
    color: white;
    overflow: hidden;
    user-select: none;

    .label, .cell-settings .label {
      position: absolute;
      top: 20%;
      left: 0;
      right: 0;
      text-align: center;
      font-weight: bold;
      pointer-events: none;
    }
    .start-stop-button {
      position: absolute;
      bottom: 4px;
      left: 0;
      right: 0;
      text-align: center;
      font-size: 150%;
    }
    .start-stop-button svg {
      color: #bbb;
    }
    .start-stop-button svg:hover {
      color: #eee;
    }

    .port {
      position: absolute;
      top: 1px;
      left: 0;
      right: 2px;
      text-align: right;
      font-size: 70%;
      color: #ddd;
    }
  }
  .cell-settings {
    background-color: teal;
    width: 0;

    .cog {
      position: absolute;
      top: 0;
      left: -75px;
      color: #bbb;
      font-size: 1000%;
      opacity: 0.6;
    }
    .text {
      position: absolute;
      top: 2em;
      left: 4em;
      font-size: 150%;
    }
  }
}

.messages {
    background-color: black;
    color: white;
}
